import * as actions from './../actionTypes/UserActionTypes';

const intitState = {
    user: undefined
}

const UserReducer = (state = intitState, action) => {
    switch (action.type) {
        case actions.LOGIN: {
            return { user: action.data }
        }
        case actions.UPDATE_USER: {
            return { user: action.data }
        }
        default: {
            return { ...state };
        }
    }
}

export default UserReducer;
